@tailwind base;
@tailwind components;
@tailwind utilities;
/* Resto de estilos */

@layer components{
    .transition-in {
        @apply transition-opacity ease-in-out duration-500;
        opacity: 1;
      }
      
      .transition-out {
        @apply transition-opacity ease-in-out duration-500;
        opacity: 0;
      }

      .green-pink-gradient {
        background: "#00cea8";
        background: linear-gradient(90.13deg, #a624ff 1.9%, #9a0afc 97.5%);
        background: -webkit-linear-gradient(-90.13deg, #a624ff 1.9%, #9a0afc 97.5%);
      }

      .scrollbar {
        scrollbar-width: thin;
        scrollbar-color: rgba(31, 0, 50, 0.982) rgba(100, 0, 158, 0.587);
      
        /* Webkit (Chrome, Safari) */
        &::-webkit-scrollbar {
          width: 12px;
        }
      
        &::-webkit-scrollbar-thumb {
          background-color: darkgrey;
          border-bottom: 6px;
        }
      
        &::-webkit-scrollbar-track {
          background-color: lightgrey;
        }
      }

      .sticky-element {
        position: sticky;
        top: 0; /* o cualquier otra posición deseada */
        z-index: 10; /* asegúrate de establecer un z-index adecuado */
      }
      
}